<template>
    <div style="min-height: 600px" v-loading="loading">
        <el-card shadow="never" style="margin-bottom: 20px">
            <el-form ref="form" :model="form" label-width="80px" :rules="ruleValidate">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="form.title" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="分类" prop="title">
                    <el-select @change="select" v-model="form.type" placeholder="请选择分类" style="padding-right: 12px;">
                        <el-option-group v-for="group in types" :key="group.label" :label="group.label">
                            <el-option v-for="item in group.options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-option-group>
                    </el-select>
                </el-form-item>
                <el-form-item label="标签" prop="title">
                    <el-tag :key="tag" v-for="tag in form.tags" closable :disable-transitions="false" @close="handleClose(tag)" style="margin-right: 10px;"> {{tag}} </el-tag>
                    <el-input @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" class="input-new-tag" size="small" style="margin-right: 10px; width: 90px; vertical-align: bottom;"></el-input>
                    <el-button v-else class="button-new-tag" size="small" @click="showInput" style="width: 90px; text-align: center;">+ New Tag</el-button>
                </el-form-item>
                <el-form-item label="简要说明" prop="description">
                    <el-input v-model="form.description" type="textarea"></el-input>
                </el-form-item>
                <el-form-item label="博客正文" prop="content">
                    <mavon-editor @imgAdd="imgAdd" style="max-height: 500px" ref="md" v-model="form.content" :subfield="false" :toolbars="mavonEditorToolbars"
                    />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit" :loading="submitButton.loading" :disabled="submitButton.disabled">修改</el-button>
                    <el-button @click="$router.push('/user/blog/main')">返回</el-button>
                </el-form-item>
            </el-form>
        </el-card>
        <token-dialog ref="tokenDialog"></token-dialog>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    import TokenDialog from '@/views/common/TokenDialog'
    import GistApi from '@/api/gist'
    export default {
        components: {
            TokenDialog
        },
        data() {
            return {
                inputVisible: false,
                inputValue: '',
                form: {
                    id: "",
                    title: "",
                    description: "",
                    content: "",
                    type: "",
                    tags: [],
                },
                ruleValidate: {
                    title: [
                        { required: true, message: '请输入标题', trigger: 'blur' },
                        { type: 'string', max: 32, message: '标题长度不大于32字符', trigger: 'change' }
                    ],
                    description: [
                        { required: true, message: '请输入博客描述', trigger: 'blur' }
                    ],
                    content: [
                        { required: true, message: '请输博客入正文', trigger: 'blur' }
                    ],
                },
                loading: true,
                submitButton: {
                    loading: false,
                    disabled: false
                },
                mavonEditorToolbars: {
                    bold: true,
                    italic: true,
                    header: true,
                    underline: true,
                    strikethrough: true,
                    mark: true,
                    superscript: true,
                    subscript: true,
                    quote: true,
                    ol: true,
                    ul: true,
                    link: true,
                    imagelink: true,
                    code: true,
                    table: true,
                    htmlcode: true,
                    trash: true,
                    alignleft: true,
                    aligncenter: true,
                    alignright: true,
                    preview: true,
                },
            }
        },
        computed: {
            ...mapGetters([
                'token',
                'types',
            ])
        },
        mounted() {
            if (!this.token) {
                this.$nextTick(() => {
                    this.$message({
                        message: '权限不足',
                        type: 'error'
                    })
                    this.$router.go(-1)
                })
                return
            }
            this.loading = true
            this.form.id = this.$route.params.id
            GistApi.single(this.form.id).then((response) => {
                let result = response.data
                for (let key in result.files) {
                    this.form['title'] = key
                    this.form['content'] = result.files[key]['content']
                    //this.form['description'] = result['description']
                    let description = result['description']
                    this.form['description'] = description.split('||')[1]
                    this.form['type'] = description.split('||')[0].split('>')[0]
                    this.form['tags'] = description.split('||')[0].split('>')[1].split('&')
                    break
                }
            }).then(() => this.loading = false)
        },
        methods: {
            handleClose(tag) {
                this.form['tags'].splice(this.form['tags'].indexOf(tag), 1)
            },
            showInput() {
                this.inputVisible = true
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus()
                })
            },
            handleInputConfirm() {
                let inputValue = this.inputValue
                if (inputValue) {
                    this.form['tags'].push(inputValue)
                }
                this.inputVisible = false
                this.inputValue = ''
            },
            select(value) {
                
            },
            imgAdd(pos, file) {
                this.$refs.md.$img2Url(pos, file.miniurl)
            },
            onSubmit() {
                if (this.token) {
                    this.publish()
                } else {
                    this.$refs.tokenDialog.open(() => {
                        this.publish()
                    })
                }
            },
            publish() {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        this.submitButton.loading = true
                        this.submitButton.disabled = true
                        let new_form = {}
                        new_form.id = this.form.id
                        new_form.title = this.form.title
                        new_form.content = this.form.content
                        new_form.description = this.form.type + '>' + this.form.tags.join('&') + '||' + this.form.description
                        GistApi.edit(new_form).then((response) => {
                            let result = response.data
                            // console.log(JSON.stringify(result))
                            this.$message({
                                message: '发表成功',
                                type: 'success'
                            })
                            this.$router.push("/user/blog/details/" + result.id)
                        }).then(() => {
                            this.submitButton.loading = false
                            this.submitButton.disabled = false
                        })
                    }
                })
            }
        }
    }
</script>

<style>
</style>


